'use client';

import { useState } from 'react';
import { Rental } from '../app/page';
import { calculateNextDueDate, calculateUpdatedAmount, formatCurrency } from '../lib/utils';

interface RentalUpdateModalProps {
  rental: Rental;
  onClose: () => void;
  onUpdate: (updatedRental: Rental) => void;
}

export default function RentalUpdateModal({ rental, onClose, onUpdate }: RentalUpdateModalProps) {
  const [percentage, setPercentage] = useState<string>('');
  const [error, setError] = useState<string | null>(null);
  const [step, setStep] = useState<'input' | 'confirm'>('input');
  const [newAmount, setNewAmount] = useState<number>(0);

  const handleCalculate = () => {
    if (!percentage || isNaN(Number(percentage))) {
      setError('Ingresa un porcentaje válido');
      return;
    }

    const percentageNum = Number(percentage);
    if (percentageNum < 0) {
      setError('El porcentaje no puede ser negativo');
      return;
    }

    setError(null);
    const calculatedAmount = calculateUpdatedAmount(rental.monto, percentageNum);
    setNewAmount(calculatedAmount);
    setStep('confirm');
  };

  const handleConfirm = (useNewAmount: boolean) => {
    const finalAmount = useNewAmount ? newAmount : rental.monto;
    const nextDueDate = calculateNextDueDate(new Date(), rental.periodValue, rental.periodType);
    
    const updatedRental: Rental = {
      ...rental,
      monto: finalAmount,
      nextDueDate: nextDueDate.toISOString()
    };

    onUpdate(updatedRental);
  };

  const handleOverlayClick = (e: React.MouseEvent) => {
    if (e.target === e.currentTarget) {
      onClose();
    }
  };

  return (
    <div 
      className="fixed inset-0 flex items-center justify-center bg-black bg-opacity-50 z-50 p-4"
      onClick={handleOverlayClick}
    >
      <div className="bg-white p-6 rounded-lg shadow-lg w-full max-w-md">
        <div className="flex justify-between items-center mb-4">
          <h2 className="text-xl font-bold text-black">Actualizar Alquiler</h2>
          <button
            onClick={onClose}
            className="text-gray-500 hover:text-gray-700 text-2xl font-bold"
            aria-label="Cerrar"
          >
            ×
          </button>
        </div>

        <div className="mb-4 p-3 bg-gray-50 rounded-lg">
          <h3 className="font-semibold text-black">{rental.nombre}</h3>
          <p className="text-gray-600">Monto actual: {formatCurrency(rental.monto)}</p>
        </div>

        {step === 'input' && (
          <>
            {error && (
              <div className="mb-4 p-3 bg-red-100 border border-red-400 text-red-700 rounded">
                {error}
              </div>
            )}

            <div className="mb-4">
              <label className="block text-sm font-medium text-gray-700 mb-2">
                Porcentaje de actualización
              </label>
              <div className="flex items-center space-x-2">
                <input
                  type="number"
                  value={percentage}
                  onChange={(e) => setPercentage(e.target.value)}
                  className="flex-1 px-3 py-2 border border-gray-300 rounded-md focus:outline-none focus:ring-2 focus:ring-black focus:border-transparent text-black"
                  placeholder="Ej: 15"
                  step="0.01"
                  min="0"
                />
                <span className="text-gray-500">%</span>
              </div>
              <p className="text-xs text-gray-500 mt-1">
                Ejemplo: 15% para inflación del período
              </p>
            </div>

            <div className="flex justify-end space-x-3">
              <button 
                onClick={onClose}
                className="px-4 py-2 border border-gray-300 text-gray-700 rounded-md hover:bg-gray-50 transition-colors"
              >
                Cancelar
              </button>
              <button 
                onClick={handleCalculate}
                className="px-4 py-2 bg-black text-white rounded-md hover:bg-gray-800 transition-colors"
              >
                Calcular
              </button>
            </div>
          </>
        )}

        {step === 'confirm' && (
          <>
            <div className="mb-6 space-y-3">
              <div className="p-3 bg-blue-50 rounded-lg">
                <p className="text-sm text-gray-600">Monto actual:</p>
                <p className="text-lg font-semibold text-black">{formatCurrency(rental.monto)}</p>
              </div>
              
              <div className="p-3 bg-green-50 rounded-lg">
                <p className="text-sm text-gray-600">Nuevo monto (+{percentage}%):</p>
                <p className="text-lg font-semibold text-green-700">{formatCurrency(newAmount)}</p>
              </div>
            </div>

            <div className="mb-6">
              <p className="text-center text-gray-700 font-medium">
                ¿Usar este nuevo monto como base para la próxima actualización?
              </p>
            </div>

            <div className="flex flex-col space-y-2">
              <button 
                onClick={() => handleConfirm(true)}
                className="w-full px-4 py-3 bg-green-600 text-white rounded-md hover:bg-green-700 transition-colors font-medium"
              >
                Sí, usar nuevo monto ({formatCurrency(newAmount)})
              </button>
              <button 
                onClick={() => handleConfirm(false)}
                className="w-full px-4 py-3 bg-gray-600 text-white rounded-md hover:bg-gray-700 transition-colors"
              >
                No, mantener monto actual ({formatCurrency(rental.monto)})
              </button>
              <button 
                onClick={() => setStep('input')}
                className="w-full px-4 py-2 border border-gray-300 text-gray-700 rounded-md hover:bg-gray-50 transition-colors"
              >
                Volver a calcular
              </button>
            </div>
          </>
        )}
      </div>
    </div>
  );
}
