'use client';

import { useState } from 'react';
import { Rental } from '../app/page';
import { notificationSystem } from '../lib/notification-system';

interface NotificationTesterProps {
  rentals: Rental[];
}

export default function NotificationTester({ rentals }: NotificationTesterProps) {
  const [testMessage, setTestMessage] = useState('');

  const testNotification = () => {
    const testRental: Rental = {
      id: 999,
      nombre: 'Prueba de Notificación',
      monto: 50000,
      detalles: 'Esta es una notificación de prueba',
      periodValue: 1,
      periodType: 'días',
      nextDueDate: new Date().toISOString(),
      createdAt: new Date().toISOString()
    };

    notificationSystem.showNotification(testRental);
    setTestMessage('✅ Notificación de prueba enviada. Si no ves nada, verifica los permisos en Configuración > Safari > Notificaciones');
  };

  const forceCheckOverdue = () => {
    notificationSystem.checkAllRentals(rentals);
    setTestMessage('🔍 Verificando alquileres vencidos...');
  };

  return (
    <div className="mb-4 p-4 bg-blue-50 border border-blue-200 rounded-lg">
      <h3 className="font-bold text-blue-800 mb-2">🔔 Prueba de Notificaciones</h3>
      
      <div className="space-y-2">
        <button
          onClick={testNotification}
          className="w-full px-3 py-2 bg-blue-600 text-white rounded text-sm hover:bg-blue-700"
        >
          Probar Notificación Ahora
        </button>
        
        <button
          onClick={forceCheckOverdue}
          className="w-full px-3 py-2 bg-green-600 text-white rounded text-sm hover:bg-green-700"
        >
          Verificar Alquileres Vencidos
        </button>
        
        {testMessage && (
          <p className="text-xs text-blue-700 mt-2">{testMessage}</p>
        )}
      </div>
      
      <div className="mt-3 text-xs text-gray-600">
        <p><strong>Para iOS:</strong></p>
        <ul className="list-disc list-inside ml-2">
          <li>Asegúrate de usar Safari</li>
          <li>Ve a Configuración {'>'} Safari {'>'} Notificaciones</li>
          <li>Habilita notificaciones para este sitio</li>
          <li>Mantén la app abierta o en segundo plano</li>
        </ul>
      </div>
    </div>
  );
}
