'use client';

import { useState } from 'react';
import { Rental } from '../app/page';
import { formatDate, formatCurrency, isOverdue } from '../lib/utils';
import { getDaysUntilDue } from '../lib/notifications';
import RentalUpdateModal from './RentalUpdateModal';
import DeleteConfirmationModal from './DeleteConfirmationModal';

interface RentalListProps {
  rentals: Rental[];
  onUpdateRental: (updatedRental: Rental) => void;
  onDeleteRental: (id: number) => void;
}

export default function RentalList({ rentals, onUpdateRental, onDeleteRental }: RentalListProps) {
  const [selectedRental, setSelectedRental] = useState<Rental | null>(null);
  const [rentalToDelete, setRentalToDelete] = useState<Rental | null>(null);

  const handleRentalClick = (rental: Rental) => {
    if (isOverdue(rental.nextDueDate)) {
      setSelectedRental(rental);
    }
  };

  const handleDeleteClick = (e: React.MouseEvent, rental: Rental) => {
    e.stopPropagation(); // Prevenir que se abra el modal de actualización
    setRentalToDelete(rental);
  };

  if (rentals.length === 0) {
    return (
      <div className="text-center py-12">
        <p className="text-gray-500 text-lg mb-2">No hay alquileres registrados</p>
        <p className="text-gray-400 text-sm">Presiona el botón "+" para agregar tu primer alquiler</p>
      </div>
    );
  }

  return (
    <>
      <div className="space-y-4">
        {rentals.map((rental) => {
          const overdueStatus = isOverdue(rental.nextDueDate);
          const daysUntilDue = getDaysUntilDue(rental);
          
          let statusColor = 'border-gray-200 bg-white';
          let statusBadge = null;
          let statusMessage = null;
          
          if (overdueStatus) {
            statusColor = 'border-red-500 bg-red-50 cursor-pointer hover:bg-red-100 shadow-md';
            statusBadge = (
              <span className="bg-red-500 text-white text-xs px-2 py-1 rounded-full font-medium">
                VENCIDO
              </span>
            );
            statusMessage = (
              <div className="mt-3 pt-2 border-t border-red-200">
                <p className="text-red-700 text-sm font-medium">
                  Toca para actualizar el monto
                </p>
              </div>
            );
          } else if (daysUntilDue <= 7 && daysUntilDue > 0) {
            statusColor = 'border-yellow-400 bg-yellow-50';
            statusBadge = (
              <span className="bg-yellow-500 text-white text-xs px-2 py-1 rounded-full font-medium">
                PRÓXIMO A VENCER
              </span>
            );
          } else if (daysUntilDue === 0) {
            statusColor = 'border-orange-500 bg-orange-50';
            statusBadge = (
              <span className="bg-orange-500 text-white text-xs px-2 py-1 rounded-full font-medium">
                VENCE HOY
              </span>
            );
          }
          
          return (
            <div
              key={rental.id}
              onClick={() => handleRentalClick(rental)}
              className={`p-4 rounded-lg border-2 transition-all duration-200 ${statusColor} relative`}
            >
              {/* Botón de eliminar - SIEMPRE VISIBLE en esquina inferior derecha */}
              <button
                onClick={(e) => handleDeleteClick(e, rental)}
                className="absolute bottom-2 right-2 w-10 h-10 bg-red-500 text-white rounded-full flex items-center justify-center shadow-lg hover:bg-red-600 transition-colors"
                aria-label="Eliminar alquiler"
              >
                🗑️
              </button>

              <div className="flex justify-between items-start mb-2">
                <h3 className="text-lg font-semibold text-black">
                  {rental.nombre}
                </h3>
                {statusBadge}
              </div>
              
              <div className="space-y-1 text-sm">
                <p className="text-gray-700">
                  <span className="font-medium">Monto:</span> {formatCurrency(rental.monto)}
                </p>
                
                <p className="text-gray-700">
                  <span className="font-medium">Próximo vencimiento:</span> {formatDate(rental.nextDueDate)}
                  {daysUntilDue > 0 && (
                    <span className="text-gray-500 ml-2">
                      ({daysUntilDue} {daysUntilDue === 1 ? 'día' : 'días'})
                    </span>
                  )}
                </p>
                
                <p className="text-gray-700">
                  <span className="font-medium">Periodicidad:</span> cada {rental.periodValue} {rental.periodType}
                </p>
                
                {rental.detalles && (
                  <p className="text-gray-600 mt-2">
                    <span className="font-medium">Detalles:</span> {rental.detalles}
                  </p>
                )}
              </div>
              
              {statusMessage}
            </div>
          );
        })}
      </div>

      {selectedRental && (
        <RentalUpdateModal
          rental={selectedRental}
          onClose={() => setSelectedRental(null)}
          onUpdate={(updatedRental) => {
            onUpdateRental(updatedRental);
            setSelectedRental(null);
          }}
        />
      )}

      {rentalToDelete && (
        <DeleteConfirmationModal
          rental={rentalToDelete}
          onClose={() => setRentalToDelete(null)}
          onConfirm={() => {
            onDeleteRental(rentalToDelete.id);
            setRentalToDelete(null);
          }}
        />
      )}
    </>
  );
}
