import { Rental } from '../app/page';

const STORAGE_KEY = 'alquileres_data';

export function getRentals(): Rental[] {
  try {
    if (typeof window === 'undefined') return [];
    
    const data = localStorage.getItem(STORAGE_KEY);
    return data ? JSON.parse(data) : [];
  } catch (error) {
    console.error('Error reading from localStorage:', error);
    return [];
  }
}

export function addRental(rental: Omit<Rental, 'id' | 'createdAt'>): Rental {
  try {
    const rentals = getRentals();
    const newRental: Rental = {
      ...rental,
      id: Date.now(),
      createdAt: new Date().toISOString(),
    };
    
    rentals.push(newRental);
    localStorage.setItem(STORAGE_KEY, JSON.stringify(rentals));
    
    return newRental;
  } catch (error) {
    console.error('Error saving rental:', error);
    throw error;
  }
}

export function updateRental(updatedRental: Rental): void {
  try {
    const rentals = getRentals();
    const index = rentals.findIndex(r => r.id === updatedRental.id);
    
    if (index !== -1) {
      rentals[index] = updatedRental;
      localStorage.setItem(STORAGE_KEY, JSON.stringify(rentals));
    }
  } catch (error) {
    console.error('Error updating rental:', error);
    throw error;
  }
}

export function deleteRental(id: number): void {
  try {
    const rentals = getRentals();
    const filteredRentals = rentals.filter(r => r.id !== id);
    localStorage.setItem(STORAGE_KEY, JSON.stringify(filteredRentals));
  } catch (error) {
    console.error('Error deleting rental:', error);
    throw error;
  }
}
