'use client';

import { useState } from 'react';
import { calculateNextDueDate } from '../lib/utils';
import { Rental } from '../app/page';

interface RentalFormProps {
  onClose: () => void;
  onSave: (rental: Omit<Rental, 'id' | 'createdAt'>) => void;
}

export default function RentalForm({ onClose, onSave }: RentalFormProps) {
  const [nombre, setNombre] = useState('');
  const [monto, setMonto] = useState<string>('');
  const [detalles, setDetalles] = useState('');
  const [periodValue, setPeriodValue] = useState<string>('');
  const [periodType, setPeriodType] = useState<'días' | 'meses'>('días');
  const [error, setError] = useState<string | null>(null);

  const handleSubmit = (e: React.FormEvent) => {
    e.preventDefault();
    
    // Validate fields
    if (!nombre.trim()) {
      setError('El nombre es obligatorio');
      return;
    }
    
    if (!monto || isNaN(Number(monto)) || Number(monto) <= 0) {
      setError('El monto debe ser un número válido mayor a 0');
      return;
    }
    
    if (!periodValue || isNaN(Number(periodValue)) || Number(periodValue) <= 0) {
      setError('La periodicidad debe ser un número válido mayor a 0');
      return;
    }
    
    setError(null);

    const now = new Date();
    const nextDueDate = calculateNextDueDate(now, Number(periodValue), periodType);
    
    const newRental: Omit<Rental, 'id' | 'createdAt'> = {
      nombre: nombre.trim(),
      monto: Number(monto),
      detalles: detalles.trim(),
      periodValue: Number(periodValue),
      periodType,
      nextDueDate: nextDueDate.toISOString()
    };

    onSave(newRental);
  };

  const handleOverlayClick = (e: React.MouseEvent) => {
    if (e.target === e.currentTarget) {
      onClose();
    }
  };

  return (
    <div 
      className="fixed inset-0 flex items-center justify-center bg-black bg-opacity-50 z-50 p-4"
      onClick={handleOverlayClick}
    >
      <div className="bg-white p-6 rounded-lg shadow-lg w-full max-w-md max-h-[90vh] overflow-y-auto">
        <div className="flex justify-between items-center mb-4">
          <h2 className="text-xl font-bold text-black">Nuevo Alquiler</h2>
          <button
            onClick={onClose}
            className="text-gray-500 hover:text-gray-700 text-2xl font-bold"
            aria-label="Cerrar"
          >
            ×
          </button>
        </div>
        
        {error && (
          <div className="mb-4 p-3 bg-red-100 border border-red-400 text-red-700 rounded">
            {error}
          </div>
        )}
        
        <form onSubmit={handleSubmit} className="space-y-4">
          <div>
            <label className="block text-sm font-medium text-gray-700 mb-1">
              Nombre *
            </label>
            <input
              type="text"
              value={nombre}
              onChange={(e) => setNombre(e.target.value)}
              className="w-full px-3 py-2 border border-gray-300 rounded-md focus:outline-none focus:ring-2 focus:ring-black focus:border-transparent text-black"
              placeholder="Ej: Departamento Centro"
              required
            />
          </div>
          
          <div>
            <label className="block text-sm font-medium text-gray-700 mb-1">
              Monto Inicial *
            </label>
            <input
              type="number"
              value={monto}
              onChange={(e) => setMonto(e.target.value)}
              className="w-full px-3 py-2 border border-gray-300 rounded-md focus:outline-none focus:ring-2 focus:ring-black focus:border-transparent text-black"
              placeholder="Ej: 50000"
              min="0"
              step="0.01"
              required
            />
          </div>
          
          <div>
            <label className="block text-sm font-medium text-gray-700 mb-1">
              Detalles
            </label>
            <textarea
              value={detalles}
              onChange={(e) => setDetalles(e.target.value)}
              className="w-full px-3 py-2 border border-gray-300 rounded-md focus:outline-none focus:ring-2 focus:ring-black focus:border-transparent text-black"
              placeholder="Información adicional..."
              rows={3}
            />
          </div>
          
          <div>
            <label className="block text-sm font-medium text-gray-700 mb-1">
              Periodicidad de Actualización *
            </label>
            <div className="flex space-x-2">
              <input
                type="number"
                value={periodValue}
                onChange={(e) => setPeriodValue(e.target.value)}
                className="flex-1 px-3 py-2 border border-gray-300 rounded-md focus:outline-none focus:ring-2 focus:ring-black focus:border-transparent text-black"
                placeholder="Ej: 3"
                min="1"
                required
              />
              <select
                value={periodType}
                onChange={(e) => setPeriodType(e.target.value as 'días' | 'meses')}
                className="flex-1 px-3 py-2 border border-gray-300 rounded-md focus:outline-none focus:ring-2 focus:ring-black focus:border-transparent text-black"
              >
                <option value="días">días</option>
                <option value="meses">meses</option>
              </select>
            </div>
            <p className="text-xs text-gray-500 mt-1">
              Ejemplo: cada {periodValue || 'X'} {periodType}
            </p>
          </div>
          
          <div className="flex justify-end space-x-3 pt-4">
            <button 
              type="button" 
              onClick={onClose}
              className="px-4 py-2 border border-gray-300 text-gray-700 rounded-md hover:bg-gray-50 transition-colors"
            >
              Cancelar
            </button>
            <button 
              type="submit"
              className="px-4 py-2 bg-black text-white rounded-md hover:bg-gray-800 transition-colors"
            >
              Guardar
            </button>
          </div>
        </form>
      </div>
    </div>
  );
}
