'use client';

import { useEffect, useState } from 'react';
import RentalForm from '../components/RentalForm';
import RentalList from '../components/RentalList';
import NotificationStatus from '../components/NotificationStatus';
import { getRentals, addRental as storageAddRental, updateRental as storageUpdateRental, deleteRental as storageDeleteRental } from '../lib/storage';
import { requestNotificationPermission } from '../lib/notifications';
import { notificationSystem } from '../lib/notification-system';
import NotificationTester from '../components/NotificationTester';

export interface Rental {
  id: number;
  nombre: string;
  monto: number;
  detalles: string;
  periodValue: number;
  periodType: 'días' | 'meses';
  nextDueDate: string;
  createdAt: string;
}

export default function Page() {
  const [rentals, setRentals] = useState<Rental[]>([]);
  const [showForm, setShowForm] = useState(false);
  const [isLoading, setIsLoading] = useState(true);

  // Load rentals from localStorage on initial render
  useEffect(() => {
    try {
      const storedRentals = getRentals();
      setRentals(storedRentals);
    } catch (error) {
      console.error('Error loading rentals:', error);
    } finally {
      setIsLoading(false);
    }
  }, []);

  // Register service worker
  useEffect(() => {
    if ('serviceWorker' in navigator) {
      navigator.serviceWorker.register('/sw.js')
        .then(() => console.log('Service Worker registered'))
        .catch((err) => console.error('SW registration error: ', err));
    }
  }, []);

  // Check for overdue rentals and notify
  useEffect(() => {
    if (rentals.length > 0) {
      notificationSystem.checkAllRentals(rentals);
    }
  }, [rentals]);

  // Set up periodic check for overdue rentals (every minute)
  useEffect(() => {
    const interval = setInterval(() => {
      if (rentals.length > 0) {
        notificationSystem.checkAllRentals(rentals);
      }
    }, 60 * 1000); // Check every minute for testing

    return () => clearInterval(interval);
  }, [rentals]);

  const addRental = (rental: Omit<Rental, 'id' | 'createdAt'>) => {
    try {
      const newRental = storageAddRental(rental);
      setRentals(getRentals());
      
      // Schedule notification for this rental
      notificationSystem.scheduleNotification(newRental);
    } catch (error) {
      console.error('Error adding rental:', error);
    }
  };

  const updateRental = (updatedRental: Rental) => {
    try {
      storageUpdateRental(updatedRental);
      setRentals(getRentals());
      
      // Schedule notification for updated rental
      notificationSystem.scheduleNotification(updatedRental);
    } catch (error) {
      console.error('Error updating rental:', error);
    }
  };

  const deleteRental = (id: number) => {
    try {
      storageDeleteRental(id);
      setRentals(getRentals());
      // Cancelar notificaciones para este alquiler
      notificationSystem.clearAlarm(id);
    } catch (error) {
      console.error('Error deleting rental:', error);
    }
  };

  if (isLoading) {
    return (
      <main className="min-h-screen bg-background text-foreground p-4 flex items-center justify-center">
        <p>Cargando...</p>
      </main>
    );
  }

  return (
    <main className="min-h-screen bg-background text-foreground p-4">
      <header className="flex justify-between items-center mb-6">
        <h1 className="text-3xl font-bold">Alquileres</h1>
        <button
          onClick={() => setShowForm(true)}
          className="w-12 h-12 bg-black text-white rounded-full text-2xl font-bold hover:bg-gray-800 transition-colors"
          aria-label="Agregar nuevo alquiler"
        >
          +
        </button>
      </header>
      
      <NotificationStatus />
      
      {process.env.NODE_ENV === 'development' && (
        <NotificationTester rentals={rentals} />
      )}
      
      <RentalList rentals={rentals} onUpdateRental={updateRental} onDeleteRental={deleteRental} />

      {showForm && (
        <RentalForm
          onClose={() => setShowForm(false)}
          onSave={(rental) => { 
            addRental(rental); 
            setShowForm(false); 
          }}
        />
      )}
    </main>
  );
}
