import { type ClassValue, clsx } from "clsx"
import { twMerge } from "tailwind-merge"

export function cn(...inputs: ClassValue[]) {
  return twMerge(clsx(inputs))
}

export function calculateNextDueDate(
  currentDate: Date, 
  periodValue: number, 
  periodType: 'días' | 'meses'
): Date {
  const nextDate = new Date(currentDate);
  
  if (periodType === 'días') {
    nextDate.setDate(nextDate.getDate() + periodValue);
  } else if (periodType === 'meses') {
    nextDate.setMonth(nextDate.getMonth() + periodValue);
  }
  
  return nextDate;
}

export function formatDate(dateString: string): string {
  try {
    const date = new Date(dateString);
    return date.toLocaleDateString('es-ES', {
      year: 'numeric',
      month: 'long',
      day: 'numeric'
    });
  } catch (error) {
    console.error('Error formatting date:', error);
    return 'Fecha inválida';
  }
}

export function isOverdue(nextDueDate: string): boolean {
  const today = new Date();
  const dueDate = new Date(nextDueDate);
  
  // Set both dates to start of day for accurate comparison
  today.setHours(0, 0, 0, 0);
  dueDate.setHours(0, 0, 0, 0);
  
  return dueDate.getTime() <= today.getTime();
}

export function calculateUpdatedAmount(currentAmount: number, percentage: number): number {
  return currentAmount + (currentAmount * percentage / 100);
}

export function formatCurrency(amount: number): string {
  return new Intl.NumberFormat('es-AR', {
    style: 'currency',
    currency: 'ARS',
    minimumFractionDigits: 0,
    maximumFractionDigits: 2
  }).format(amount);
}
