'use client';

import { useState, useEffect } from 'react';
import { requestNotificationPermission, checkNotificationPermission } from '../lib/notifications';

export default function NotificationStatus() {
  const [permission, setPermission] = useState<string>('default');
  const [isRequesting, setIsRequesting] = useState(false);

  useEffect(() => {
    setPermission(checkNotificationPermission());
  }, []);

  const handleRequestPermission = async () => {
    setIsRequesting(true);
    const granted = await requestNotificationPermission();
    setPermission(checkNotificationPermission());
    setIsRequesting(false);
  };

  if (permission === 'not-supported') {
    return (
      <div className="mb-4 p-3 bg-yellow-50 border border-yellow-200 rounded-lg">
        <p className="text-yellow-800 text-sm">
          ⚠️ Este navegador no soporta notificaciones. Usa Safari en iOS para recibir recordatorios.
        </p>
      </div>
    );
  }

  if (permission === 'denied') {
    return (
      <div className="mb-4 p-3 bg-red-50 border border-red-200 rounded-lg">
        <p className="text-red-800 text-sm mb-2">
          🚫 Las notificaciones están bloqueadas
        </p>
        <p className="text-red-600 text-xs">
          Ve a Configuración → Safari → Notificaciones para habilitarlas
        </p>
      </div>
    );
  }

  if (permission === 'granted') {
    return (
      <div className="mb-4 p-3 bg-green-50 border border-green-200 rounded-lg">
        <p className="text-green-800 text-sm">
          ✅ Notificaciones activadas - Recibirás recordatorios de vencimientos
        </p>
      </div>
    );
  }

  // permission === 'default'
  return (
    <div className="mb-4 p-3 bg-blue-50 border border-blue-200 rounded-lg">
      <p className="text-blue-800 text-sm mb-3">
        🔔 Activa las notificaciones para recibir recordatorios cuando venzan tus alquileres
      </p>
      <button
        onClick={handleRequestPermission}
        disabled={isRequesting}
        className="w-full px-4 py-2 bg-blue-600 text-white rounded-md hover:bg-blue-700 transition-colors disabled:opacity-50"
      >
        {isRequesting ? 'Solicitando permisos...' : 'Activar Notificaciones'}
      </button>
    </div>
  );
}
