'use client';

import { Rental } from '../app/page';

interface DeleteConfirmationModalProps {
  rental: Rental;
  onClose: () => void;
  onConfirm: () => void;
}

export default function DeleteConfirmationModal({ rental, onClose, onConfirm }: DeleteConfirmationModalProps) {
  const handleOverlayClick = (e: React.MouseEvent) => {
    if (e.target === e.currentTarget) {
      onClose();
    }
  };

  return (
    <div 
      className="fixed inset-0 flex items-center justify-center bg-black bg-opacity-50 z-50 p-4"
      onClick={handleOverlayClick}
    >
      <div className="bg-white p-6 rounded-lg shadow-lg w-full max-w-md">
        <h2 className="text-xl font-bold text-black mb-4">¿Eliminar Alquiler?</h2>
        
        <div className="mb-6">
          <p className="text-gray-700 mb-2">
            ¿Estás seguro que querés eliminar este alquiler?
          </p>
          <div className="p-3 bg-gray-50 rounded-lg">
            <p className="font-semibold text-black">{rental.nombre}</p>
            <p className="text-sm text-gray-600">Monto: ${rental.monto.toLocaleString()}</p>
          </div>
        </div>

        <div className="flex justify-end space-x-3">
          <button 
            onClick={onClose}
            className="px-4 py-2 border border-gray-300 text-gray-700 rounded-md hover:bg-gray-50 transition-colors"
          >
            No
          </button>
          <button 
            onClick={() => {
              onConfirm();
              onClose();
            }}
            className="px-4 py-2 bg-red-600 text-white rounded-md hover:bg-red-700 transition-colors"
          >
            Sí, eliminar
          </button>
        </div>
      </div>
    </div>
  );
}
